.box {
    position: relative;
    top: 0;
    opacity: 5;
    float: left;
    padding: 20px 50px 20px 50px;
    width: 100%;
    background: rgba(255, 255, 255, .85);
    border-radius: 10px;
    transform: scale(1);
    -webkit-transform: scale(1);
    -ms-transform: scale(1);
    z-index: 5;
}

.box.back {
    transform: scale(.95);
    -webkit-transform: scale(.95);
    -ms-transform: scale(.95);
    top: -20px;
    opacity: .8;
    z-index: -1;
}

.box:before {
    content: "";
    width: 100%;
    height: 20px;
    border-radius: 10px;
    position: absolute;
    top: -10px;
    background: rgba(255, 255, 255, 0);
    left: 0;
    transform: scale(.95);
    -webkit-transform: scale(.95);
    -ms-transform: scale(.95);
    z-index: -1;
}

.overbox .title {
    color: #fff;
}

.overbox .title:before {
    background: #fff;
}

.title {
    width: 100%;
    float: left;
    line-height: 46px;
    font-size: 34px;
    font-weight: 700;
    letter-spacing: 2px;
    color: #fe4320;
    position: relative;
}

.title:before {
    content: "";
    width: 5px;
    height: 100%;
    position: absolute;
    top: 0;
    left: -50px;
    background: #fe4320;
}

.input,
.input label,
.input input,
.input .spin,
.button,
.button button .button.login button i.fa,
.material-button .shape:before,
.material-button .shape:after,
.button.login button {
    -o-transition: 300ms cubic-bezier(.4, 0, .2, 1);
    transition: 300ms cubic-bezier(.4, 0, .2, 1);
    -webkit-transition: 300ms cubic-bezier(.4, 0, .2, 1);
    -ms-transition: 300ms cubic-bezier(.4, 0, .2, 1);
}

.material-button,
.alt-2,
.material-button .shape,
.alt-2 .shape,
.box {
    -o-transition: 400ms cubic-bezier(.4, 0, .2, 1);
    transition: 400ms cubic-bezier(.4, 0, .2, 1);
    -webkit-transition: 400ms cubic-bezier(.4, 0, .2, 1);
    -ms-transition: 400ms cubic-bezier(.4, 0, .2, 1);
}

.input,
.input label,
.input input,
.input .spin,
.button,
.button button {
    width: 100%;
    float: left;
}

.input,
.button {
    margin-top: 30px;
    height: 70px;
}

.input,
.input input,
.button,
.button button {
    position: relative;
}

.input input {
    height: 60px;
    top: 10px;
    border: none;
    background: transparent;
}

.input input,
.input label,
.button button {
    font-family: 'Roboto', sans-serif;
    font-size: 24px;
    color: rgba(0, 0, 0, 0.8);
    font-weight: 300;
}

.input:before,
.input .spin {
    width: 100%;
    height: 1px;
    position: absolute;
    bottom: 0;
    left: 0;
}

.input:before {
    content: "";
    background: rgba(0, 0, 0, 0.1);
    z-index: 3;
}

.input .spin {
    background: #fe4320;
    z-index: 4;
    width: 0;
}

.overbox .input .spin {
    background: rgba(255, 255, 255, 1);
}

.overbox .input:before {
    background: rgba(255, 255, 255, 0.5);
}

.input label {
    position: absolute;
    top: 10px;
    left: 0;
    z-index: 2;
    cursor: pointer;
    line-height: 60px;
}

.button.login {
    width: 60%;
    left: 20%;
}

.button.login button,
.button button {
    width: 100%;
    line-height: 64px;
    left: 0%;
    background-color: transparent;
    border: 3px solid rgba(0, 0, 0, 0.1);
    font-weight: 900;
    font-size: 18px;
    color: rgba(0, 0, 0, 0.2);
}

.button.login {
    margin-top: 30px;
}

.button {
    margin-top: 20px;
}

.button button {
    background-color: #fff;
    color: #fe4320;
    border: none;
}

.button.login button.active {
    border: 3px solid transparent;
    color: #fff !important;
}

.button.login button.active span {
    opacity: 0;
    transform: scale(0);
    -webkit-transform: scale(0);
    -ms-transform: scale(0);
}

.button.login button.active i.fa {
    opacity: 1;
    transform: scale(1) rotate(-0deg);
    -webkit-transform: scale(1) rotate(-0deg);
    -ms-transform: scale(1) rotate(-0deg);
}

.button.login button i.fa {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    line-height: 60px;
    transform: scale(0) rotate(-45deg);
    -webkit-transform: scale(0) rotate(-45deg);
    -ms-transform: scale(0) rotate(-45deg);
}

.button.login button:hover {
    color: #fe4320;
    border-color: #fe4320;
}

.button {
    margin: 40px 0;
    overflow: hidden;
    z-index: 2;
}

.button button {
    cursor: pointer;
    position: relative;
    z-index: 2;
}

.pass-forgot {
    width: 100%;
    float: left;
    text-align: center;
    color: #fe4320;
    font-size: 18px;
}

.click-efect {
    position: absolute;
    top: 0;
    left: 0;
    background: #fe4320;
    border-radius: 50%;
}

.overbox {
    width: 400px;
    height: 100%;
    position: absolute;
    top: 0;
    left: -20px;
    overflow: inherit;
    border-radius: 10px;
    padding: 10px 50px 10px 50px;
}

.overbox .title,
.overbox .button,
.overbox .input {
    z-index: 111;
    position: relative;
    color: #fff !important;
    display: none;
}

.overbox .title {
    width: 80%;
}

.overbox .input {
    margin-top: 20px;
}

.overbox .input input,
.overbox .input label {
    color: #fff;
}

.overbox .material-button,
.overbox .material-button .shape,
.overbox .alt-2,
.overbox .alt-2 .shape {
    display: block;
}

.material-button,
.alt-2 {
    width: 140px;
    height: 140px;
    border-radius: 50%;
    background: #fe4320;
    position: absolute;
    top: 40px;
    right: -50px;
    cursor: pointer;
    z-index: 100;
    transform: translate(0%, 0%);
    -webkit-transform: translate(0%, 0%);
    -ms-transform: translate(0%, 0%);
}

.material-button .shape,
.alt-2 .shape {
    position: absolute;
    top: 0;
    right: 0;
    width: 82px;
    height: 100px;
}

.material-button .shape:before,
.alt-2 .shape:before,
.material-button .shape:after,
.alt-2 .shape:after {

    content: "";
    background: #fff;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%) rotate(360deg);
    -webkit-transform: translate(-50%, -50%) rotate(360deg);
    -ms-transform: translate(-50%, -50%) rotate(360deg);
}

.material-button .shape:before,
.alt-2 .shape:before {
    width: 25px;
    height: 4px;
}

.material-button .shape:after,
.alt-2 .shape:after {
    height: 25px;
    width: 4px;
}

.material-button.active,
.alt-2.active {
    top: 50%;
    right: 50%;
    transform: translate(50%, -50%) rotate(0deg);
    -webkit-transform: translate(50%, -50%) rotate(0deg);
    -ms-transform: translate(50%, -50%) rotate(0deg);
}

body {
    background-image: url(https://78.media.tumblr.com/28be845fb9474e057c9d274b70c0f91a/tumblr_om7dgjPUnR1u3bn17o1_1280.gif);
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    min-height: 100vh;
    font-family: 'Roboto', sans-serif;
}

body,
html {
    overflow: hidden;
}

.materialContainerLogin {
    width: 100%;
    max-width: 360px;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    -webkit-transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
}

*,
*:after,
*::before {
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    text-decoration: none;
    list-style-type: none;
    outline: none;
}


/* Icons */

.names {
    color:#fff;
    border-radius:50%;
    text-align:center;
    text-decoration:none;
    font-family:fontawesome;
    position: relative;
    display: inline-block;
    width:52px;
    height:52px;
    padding-top:12px;
    margin-left: 1230px;
    margin-top: 590px;
    -o-transition:all .5s;
    -webkit-transition: all .5s;
    transition: all .5s;
    -webkit-font-smoothing: antialiased;
}

a:hover {



}

/* pop-up text */

a span {
    color:#666;
    position:absolute;
    font-family:sans-serif;
    bottom:0;
    left:-45px;
    right:-45px;
    padding:9px 7px;
    z-index:0;
    font-size:14px;
    border-radius:2px;
    background:#fff;
    visibility:hidden;
    opacity:0;
    -o-transition:all .5s cubic-bezier(0.68, -0.55, 0.265, 1.55);
    -webkit-transition: all .5s cubic-bezier(0.68, -0.55, 0.265, 1.55);
    transition: all .5s cubic-bezier(0.68, -0.55, 0.265, 1.55);
}

/* pop-up text arrow */

a span:before {
    content:'';
    width: 0px;
    height: 0;
    border-left: 5px solid transparent;
    border-right: 5px solid transparent;
    border-top: 5px solid #fff;
    position:absolute;
    bottom:-5px;
    left:65px;
}

/* text pops up when icon is in hover state */

a:hover span {
    bottom:70px;
    visibility:visible;
    opacity:1;
}

.loader{
    width:100%;
    height:100%;
    background-color: #fff;
    position:fixed;
    top:0px;
    left: 0px;
    z-index:1000;
}
.loader .inner{
    width: 100%;
    height: 100%;

    background-color: #3f2622;
}

.back-login{
    background-image: url(https://78.media.tumblr.com/28be845fb9474e057c9d274b70c0f91a/tumblr_om7dgjPUnR1u3bn17o1_1280.gif);
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    min-height: 100vh;
}



